<template>
    <div class="list">
      <Search></Search>
      <div class="commonNav">
        <div class="wrapper">
          <div class="part">
            <router-link to="/" >
              <span>首页</span>
              <i></i>
            </router-link>
            <router-link to="/list" class="active">
              <span>找项目</span>
              <img class="hot" src="../../assets/hot.png">
              <i></i>
            </router-link>
            <router-link to="/rankingList" >
              <span>排行榜</span>
              <i></i>
            </router-link>
            <router-link to="/news">
              <span>行业资讯</span>
              <i></i>
            </router-link>
          </div>
        </div>
      </div>
      <div class="wrapper clearfix">
        <div class="classifyList">
          <div class="classify" >
            <div class="left">
              <span class="title">行业分类</span>
              <span @click="cateChange" data-id="" :class="[cateid == ''? 'currentClassify':'']">不限</span>
            </div>
            <div class="right" @click="cateChange">
                <span v-for="item in cateList" :data-id="item.id" :key="'cata'+item.id" :class="[cateid == item.id ? 'currentClassify':'']">{{item.name}}</span>
            </div>
          </div>
          <div class="classify" v-if="cateid != ''" >
            <div class="left">
              <span class="title">二级分类</span>
              <span data-id="" @click="childChange" :class="[childid == '' ? 'currentClassify':'']">不限</span>
            </div>
            <div class="right" @click="childChange">
              <span v-for="item in childList" :data-id="item.id" :class="[childid == item.id ? 'currentClassify':'']" :key="'child'+item.id">{{item.name}}</span>
            </div>
          </div>
          <div class="classify">
            <div class="left">
              <span class="title">投资金额</span>
              <span data-money="0" :class="[money == 0 ? 'currentClassify':'']" @click="moneyChange">不限</span>
            </div>
            <div class="right" @click="moneyChange">
              <span data-money="1" :class="[money == 1 ? 'currentClassify':'']">1万以下</span>
              <span data-money="2" :class="[money == 2 ? 'currentClassify':'']">1-10万</span>
              <span data-money="3" :class="[money == 3 ? 'currentClassify':'']">10-20万</span>
              <span data-money="4" :class="[money == 4 ? 'currentClassify':'']">20-50万</span>
              <span data-money="5" :class="[money == 5 ? 'currentClassify':'']">50-100万</span>
              <span data-money="6" :class="[money == 6 ? 'currentClassify':'']">100万以上</span>
            </div>
          </div>
        </div>

        <div class="content fl clearfix">
          <div class="title clearfix">
            <div class="fl" >加盟项目</div>
            <el-select v-model="order" @change="orderChange()" placeholder="请选择" class="select fr">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
          <router-link :to="'/detail?id='+item.id" class="item" v-for="item in data" :key="'pro'+item.id">
              <img :src="item.img" alt="">
              <div class="right">
                <div class="name clearfix">
                  <span class="fl">{{item.item_name}}</span>
                  <span class="money fr">￥{{item.money}}万</span>
                </div>
                <div class="detail">
                  <div class="info">
                      所属行业：<span>{{item.cate_name}}</span>
                  </div>
                  <div class="info">
                    直营店：<span>{{item.shop_count}}家</span>
                  </div>
                  <div class="info">
                    申请人数：<span>{{item.apply}}</span>
                  </div>

                  <div class="info">
                    加盟店：<span>{{item.fran_store_num}}家</span>
                  </div>
                </div>
              </div>
          </router-link>

          <el-pagination
            background
            layout="prev, pager, next"
            :total="count"
            class="pages"
            @current-change="pageChange"
          >
          </el-pagination>
        </div>
        <div v-for="item in adve[0]" :key="'adve0'+item.id">
              <router-link :to="item.link" v-if="item.type != 1"><img class="fr advertising" :src="item.img"></router-link>
              <a :href="item.link" v-else target="_blank"><img class="fr advertising" :src="item.img"></a>
        </div>
        <HotOrder :more="false" class="hotOrder fr" ></HotOrder>
        <IndustryList class="IndustryList fr"></IndustryList>
      </div>
    </div>
</template>

<script>
  import Search from '@/components/Search'
  import HotOrder from '@/components/HotOrder'
  import IndustryList from '@/components/IndustryList'
  import api from '@/api/product-api'
  import commonApi from '@/api/common-api'

  export default {
      name: "index.vue",
      data() {
        return {
          data: '',
          cateid : '',
          childid : '',
          cateList: [],
          childList: [],
          keyword: '',
          money: 0,
          page: 0,
          options: [{
            value: '0',
            label: '最新'
          }, {
            value: '1',
            label: '热度'
          }],
          order: '0',
          count: 0,
          adve:''
        }
      },
      mounted:function() {

        const {cateid='' , childid='',keyword='',order='0'} = this.$route.query;
        this.cateid = cateid;
        this.childid = childid;
        this.keyword = keyword;
        this.order = order;
        this.getCateList();
        if(cateid){
          this.getChildList();
        }
        this.getData();
        this.getAdve();
      },
      beforeRouteUpdate(to,from,next){
        const {cateid='' , childid='',keyword='',order='0'} = to.query;
        this.cateid = cateid;
        this.childid = childid;
        this.keyword = keyword;
        this.order = order;
        this.getCateList();
        if(cateid){
          this.getChildList();
        }
        this.getData();
        this.getAdve();
        next();
      },
      methods: {
        getData(){
          const {childid='',money,keyword,order,page} = this;
          api.getProductList({cate_id:childid,money,keyword,order,page}).then(data => {
            this.count = data.count;
            this.data = data.data;
          });
        },
        getCateList(){
            api.getCate({d:0}).then(data => {
              this.cateList = data;
            });
        },
        getChildList(){
          const {cateid} = this;
          api.getCate({d:cateid}).then(data => {
            this.childList = data;
          });
        },
        /**
         * 获取广告
         */
        getAdve:function(){
            commonApi.getAdve({wp:3}).then(data=>{
              this.adve = data;
            })
        },
        /**
         * 分类改变事件
         */
        cateChange: function(e){
          this.cateid = e.target.dataset.id;
          this.childid = '';
          this.getChildList();
          this.getData();
        },
        /**
         * 二级分类改变事件
         */
        childChange: function(e){
          this.childid = e.target.dataset.id;
          this.getData();
        },
        /**
         * 投资金额改变事件
         */
        moneyChange: function (e){
          this.money = e.target.dataset.money;
          this.getData();
        },
        /**
         * 更改排序方式
         */
        orderChange: function(){
          this.getData();
        },
        /**
         * 点击分页
         * @param e
         */
        pageChange:function(e){
          this.page = e-1;
          window.scrollTo(0,0);
          this.getData();
        }

      },
      components:{
        Search,
        HotOrder,
        IndustryList
      }
    }
</script>

<style lang="less" type="text/less" scoped>
  .list{
    background-color: #F4F5FA;
  }
  /* 行业分类*/
  .classifyList{
    background-color: #FFF;
    padding: 0 24px;
    margin-top: 10px;
    .classify{
      display: flex;
      flex: none;
      border-bottom: 1px solid #E3E4E5;
      padding-top: 20px;
      padding-bottom: 10px;
      .title{
        color: #000;
        font-weight: bold;
        cursor: default;
      }
      span{
        color: #333;
        line-height: 26px;
        white-space: nowrap;
        padding: 0 10px;
        cursor: pointer;

      }
      .currentClassify{
        color: #fff;
        background-color: #4F74F4 ;
        border-radius: 2px;
      }
      .left{
        width: 125px;
        flex-shrink: 0;
      }
      .right{
        display: flex;
        flex-wrap: wrap;
        span{
          margin-bottom: 10px;
        }
      }
    }
    .classify:last-child{
      border: 0;
    }
  }
  /* 行业分类*/

  /* tabbar */
  .commonNav {
    width: 100%;
    height: 40px;
    line-height: 40px;
    background: #2F333B;
    font-size: 16px;
    color: #fff;
    .part{
      a{
        float: left;
        width: 160px;
        height: 40px;
        position: relative;
        color: #fff;
        text-align: center;
        .hot{
          position: absolute;
          top: 4px;
          margin-left: 6px;
        }
      }
      a.active{
        background: #1A1D21;
      i{
        width: 34px;
        height: 2px;
        background: -webkit-gradient(linear, 100% 0, 0 0, from(#579EFA), to(#4F74F4));
        background: -webkit-linear-gradient(to bottom, #579EFA, #4F74F4);
        background: -moz-linear-gradient(to bottom, #579EFA, #4F74F4);
        background: -o-linear-gradient(to bottom, #579EFA, #4F74F4);
        background: linear-gradient(to bottom, #579EFA, #4F74F4);
        position: absolute;
        left: 50%;
        margin-left: -17px;
        bottom: 2px;
        z-index: 1;
      }
    }
  }
  .issueBtn{
    float: right;
    color: #FEAB17;
  }
  }
  /* tabbar */
  .content{
    width: 820px;
    min-height: 800px;
    padding: 0 34px;
    background-color: #fff;
    margin-top: 11px;
    margin-bottom: 81px;
    .title{
      font-size: 16px;
      font-weight: bold;
      line-height: 40px;
      padding: 10px 0;
      border-bottom: 1px solid #E3E4E5;
    }
    .item{
      display: flex;
      font-size: 14px;
      color: #999;
      padding: 20px 0;
      border-bottom: 1px solid #E3E4E5;
      .right{
        width: 700px;
        margin-left: 22px;
        .name{
          font-size: 18px;
          color: #333;
          font-weight: bold;
          margin-top: 24px;
          .money{
            color: #FF7E00;
            font-size: 16px;

          }
        }
      }
      img{
        width: 140px;
        height: 140px;
      }
      .detail{
        width: 320px;
        margin-top: 15px;

        .info{
          display: inline-block;
          margin-bottom: 10px;
          span{
            display: inline-block;
            width: 80px;
          }
        }
      }
    }
  }
  .advertising{
    width: 295px;
    height: 210px;
    margin-top: 11px;
  }
  .hotOrder,.IndustryList{
    margin-top: 11px;
  }
  .select{
    width: 100px;
  }
  .hotOrder{
    width: 295px;
  }
  .pages{
    text-align: center;
    margin: 34px 0;
  }
</style>



